import Header from './Header'
import Layout from '../components/Layout'
import About from '../components/About'
import Skills from '../components/Skills'
import Experience from '../components/Experience'
import Footer from '../components/Footer'
import Nav from '../components/Nav'
import Head from 'next/head'

class App extends React.Component {

	constructor(props) {
		super(props)
	}

	componentDidMount() {
  		scrollToComponent(this.Blue, { offset: 0, align: 'middle', duration: 500, ease:'inCirc' });
	}

	render () {
		return (
			<Layout>
				<Head>
					<title>{this.props.meta.title}</title>
					<meta name="viewport" content="width=device-width, initial-scale=1"/>
					<link href="https://fonts.googleapis.com/css?family=Raleway:300,400,600,700" rel="stylesheet" />
				</Head>
				<Header {...this.props.sectionHeader}
				> 
					<Nav {...this.props.navigation}
						 {...this.state}
					 />
				</Header>
				<About {...this.props.sectionAbout}
					   {...this.state}
					   ref={(section) => { this.aboutRef = section }}
				/>
				<Skills {...this.props.sectionSkills} {...this.state}/>
				<Experience {...this.props.sectionExperience} {...this.state}/>
				<Footer/>
			</Layout>
		)
	}
}

export default App
